}
a {
  color: #fff
}
.head {
    width: 100%;
    height: 90px;
    min-width: 1903px;
    background:#222;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    font-size: 16px;
    -webkit-transition: all .8s;
    transition: all .8s;
}
/* .head-show {
    opacity: 1;
    visibility: visible;
  }
  .head-hidden {
    opacity: 0;
    visibility: hidden;
  } */
.head-box {
      margin: 0 58px 0 380px;
}
.logo {
        float: left;
        margin-top:24px;
}
.logo img {
        height:42px;
        width: 38px;
}
.header-right {
        position: relative;
        float: right;
        height: 90px;
        padding-right:150px;
        /* display: inline-block; */
        -webkit-transition: all 1.5s;
        transition: all 1.5s;
        /* visibility:visible;
        opacity: 1; */
        right: 50px;
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
}
.header-li {
          position: relative;
          float: left;
          margin-right:35px;
          line-height: 90px;
          white-space: nowrap;
          color:#fff;
}
/* 这里是实现教学资源的下拉框 */
/* start */
.selected {
          position: relative;
          float: left;
          margin-right: 35px;
          line-height: 90px;
          color:#fff;
          overflow: hidden;
          cursor: default;
}
.selected div {
          height: 0;
          width: 64px;
          background: #222;
          -webkit-transition: all .8s;
          transition: all .8s;
          line-height: 20px;
          font-size: 13px;
          line-height: 50px;
          text-align: center;
}
.options {
            display: inline-block;
            height: 50px;
            width: 100%;
            color: #fff;
}
.options:hover {
            background:#555;
}
/* End  */
.header-li::after {
          content: "";
          width: 0;
          height:2px;
          background-color: #fff;
          position: absolute;
          bottom: 2px;
          left: 0;
          -webkit-transition: width .4s;
          transition: width .4s;
          /* box-sizing: border-box; */
}
.header-li:hover::after {
          width: 100%;
          -webkit-transition: width .4s;
          transition: width .4s;
}
/* .header-li::after :hover {
          width: 20px;
          border-color: black;
        } */
.header-li a {
          height: 100%;
          position: relative;
          padding: 0 15px;
          cursor: pointer;
}
.header-li span {
          font-size: 16px;
}
.menuBtn {
        position:absolute;
        right: 700px;
        opacity: 0;
        top: 10px;
        visibility: hidden;
        height: 70px;
        width: 70px;
        -webkit-transform: rotate(-45deg);
                transform: rotate(-45deg);
        -webkit-transition: all .8s;
        transition: all .8s;
        border-radius: 50%;
        cursor: pointer;
        background: #fff;
        -webkit-box-shadow: 0 4px 15px rgba(0,0,0,0.15);
                box-shadow: 0 4px 15px rgba(0,0,0,0.15);
        border: 0;
        text-align: center
}


